.loading-indicator-container {
    position: fixed;
    right: 1.5em;
    top: 0.5em;
    z-index: 10;
    opacity: 0.95;
    -webkit-filter: blur(3px);
            filter: blur(3px);
}

.loading-indicator.hidden {
    opacity: 0;
    -webkit-transition: opacity 0.25s ease-in-out;
    -o-transition: opacity 0.25s ease-in-out;
    transition: opacity 0.25s ease-in-out;
}

.loading-indicator {
    opacity: 0.95;
    -webkit-transition: opacity 0.25s ease-in-out;
    -o-transition: opacity 0.25s ease-in-out;
    transition: opacity 0.25s ease-in-out;
}



