.product {
    border: 1px solid #aaa;
    border-radius: 5px;
    padding: 0.25em;
    margin-bottom: 2em;
    cursor: pointer;
    -webkit-transition: 0.25s ease-in-out;
    -o-transition: 0.25s ease-in-out;
    transition: 0.25s ease-in-out;
    max-height: 420px;
    background-color: white;
    -webkit-user-select : none;
       -moz-user-select : none;
        -ms-user-select : none;
            user-select : none;
}


.product h5{
    overflow: hidden;
    word-break: break-all;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
}

.product:hover {
    -webkit-transform: scale(1.05, 1.05);
        -ms-transform: scale(1.05, 1.05);
            transform: scale(1.05, 1.05);
    -webkit-box-shadow: 0 5px 8px 0 rgba(102, 102, 102, 0.75);
            box-shadow: 0 5px 8px 0 rgba(102, 102, 102, 0.75);
}

.product .loading-indicator {
    width: 100%;
    height: 120px;
}

.product img.loading-icon{
    padding-top: 30px;
    max-height: 50%;
    margin : auto;
    -webkit-filter: blur(1px);
            filter: blur(1px);
}

.product img.product-image {
    width: 100%;
    height: 120px;
    -o-object-fit: cover;
       object-fit: cover;
}

.product p {
    text-align: justify;
    height: 128px;
    max-height: 128px;
    overflow-y: auto;
    padding: 1em;
}


