.notification-container {
    position: fixed;
    right: 0px;
    left: 0px;
    bottom: 1em;
    width: 30%;
    min-width: 120px;
    margin: 0 auto;
    z-index: 10;
}

.notification-container .notification {
    padding: 10px;
    color: #fff;
    text-align: center;
    border-radius: 5px ;
    font-size: 1em;
    -webkit-box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.75);
            box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.75);
    margin-bottom: 10px;
}

.notification-container .notification:last-child {
    margin-bottom: 0;
}

.notification-container .notification .notification-icon {
    display: inline-block;
    width: 50px;
    font-size: 1.3em;
    vertical-align: middle;
}

.notification-container .notification .notification-message {
    display: inline-block;
}

.notification .timeline{
    display: block;
    opacity: 0.6;
    border-bottom: 2px solid white;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-name: decreasingTimeline;
            animation-name: decreasingTimeline;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes decreasingTimeline {
    from {
        width: 100%;
    }

    to {
        width: 0;
    }
}

@keyframes decreasingTimeline {
    from {
        width: 100%;
    }

    to {
        width: 0;
    }
}

.notification-container .notification.success-notification {
    background-color: #3e7b82;
}

.notification-container .notification.error-notification {
    background-color: rgb(216, 136, 135);
}

.notification-container .error-notification .close-message {
    display: inline-block;
    margin-left: 15px;
    vertical-align: middle;
    cursor: pointer;
    color: rgba(255, 255, 255, 0.69);
}



